:host {
  @include reset-host;
}

:host .share-banner {
  display: flex;
  gap: rem(24);
  align-items: flex-start;
  justify-content: space-between;
  padding: rem(44) 0;

  @include mq('max', 'sm') {
    flex-direction: column;
    align-items: center;
  }
}

:host .button-wrapper {
  display: grid;
  flex-wrap: wrap;
  grid-template-columns: repeat(4, 1fr);
  gap: rem(16);
  align-items: center;
  justify-content: flex-start;

  @include mq('max', 'sm') {
    gap: rem(27);
  }

  bs-icon {
    padding: rem(7);
    font-size: rem(24);
    color: $gray-700;
    border: 1px solid $gray-800;
    border-radius: 50%;
  }
}

:host .social-section {
  display: flex;
  flex-direction: column;
  gap: rem(12);
}

:host .social-item {
  display: flex;
  gap: rem(12);
  align-items: center;
}

:host .text {
  flex: 1;
}
