import { Component, Host, h, Prop, State } from '@stencil/core';
import { TitleSize, TitleTag } from '../../atoms/title/title.types';
import { sharingButtons } from './share-banner.data';
import { SharingTypes } from './share-banner.types';

/**
 * Share banner component.
 */
@Component({
  tag: 'bs-share-banner',
  styleUrl: 'share-banner.scss',
  shadow: true,
})
export class BsShareBanner {
  /** State */
  @State() currentUrl: string;

  /**
   * Section's title
   */
  @Prop() sectionTitle: string;

  /** Methods */
  private handleShareClick(type: SharingTypes) {
    if (type === 'email') {
      this.shareByEmail();
    } else {
      this.shareToSocials(type);
    }
  }

  private shareByEmail() {
    const encodedCurrentUrl = encodeURIComponent(this.currentUrl);

    window.open(
      `mailto:?body=${encodedCurrentUrl}`,
      '_blank' // <- This is what makes it open in a new window.
    );
  }

  private shareToSocials(socialType: SharingTypes) {
    const encodedCurrentUrl = encodeURIComponent(this.currentUrl);
    let sharingLinkUrl: string;

    switch (socialType) {
      case 'facebook':
        sharingLinkUrl = `https://www.facebook.com/sharer/sharer.php?u=${encodedCurrentUrl}`;
        break;

      case 'twitter':
        sharingLinkUrl = `https://twitter.com/intent/tweet?url=${encodedCurrentUrl}`;
        break;

      case 'linkedin':
        sharingLinkUrl = `https://www.linkedin.com/sharing/share-offsite/?url=${encodedCurrentUrl}`;
        break;

      default:
        break;
    }

    window.open(sharingLinkUrl, 'myWindow', 'width=550, height=400');
  }

  /** Lifecycle Hooks */
  componentDidLoad() {
    this.currentUrl = window.location.href;
  }

  render() {
    return (
      <Host>
        <div class="share-banner">
          {/* Title */}
          <bs-title text={this.sectionTitle} tag={TitleTag.H2} size={TitleSize.Head3} />

          {/* Sharing buttons */}
          <div class="button-wrapper">
            {sharingButtons.map((item) => (
              <bs-click-action class="icon" onBsClickAction={() => this.handleShareClick(item.id)}>
                <bs-icon name={item.icon} />
              </bs-click-action>
            ))}
          </div>
        </div>
      </Host>
    );
  }
}
